import TaskHeader from './components/Header'

export default {
	name: 'Detail',
	components: {
		TaskHeader
	},
	data() {
		return {
			taskList: []
		}
	},
	mounted () {
		this.getListData()
	},
	methods: {
		getListData () {
			uni.request({
					url: '/static/data/task.json', 
					success: (res) => {
						if(res.data.code.toLocaleUpperCase() === 'SUCCESS'){
							this.taskList = res.data.result;
						}
					}
			});
		}
	}
}
